.Send {
  position: absolute;
  margin: auto;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  width: 70vw;
  height: 35vw;
  opacity: 0.6;
  -webkit-box-shadow: 0 0 8px 0 black;
          box-shadow: 0 0 8px 0 black;
  background-color: black;
  color: white;
  font-size: 3vw;
  padding: 1.5vw;
}
.Send__table {
  width: 100%;
  height: 100%;
  border-collapse: collapse;
}
.Send__table__main{

  height: 3vw;
}
.Send__table td {
  /* border: 1px solid red; */
  width: 50%;
  vertical-align: middle;
}
.Send__table td:nth-child(2) {
  text-align: right;
}
.Send__checkbox {
  position: relative;
  display: inline-block;
  width: 5vw;
  height: 3vw;
}
.Send__checkbox input {
  display: none;
}
.Send__checkbox__slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: white;
  -webkit-transition: 0.4s;
  transition: 0.4s;
}
.Send__checkbox__slider:before {
  position: absolute;
  content: "";
  height: 2.4vw;
  width: 2.4vw;
  left: 0.3vw;
  top: 0.3vw;
  background-color: black;
  -webkit-transition: 0.4s;
  transition: 0.4s;
}
.Send__checkbox__input:checked + .Send__checkbox__slider {
  background-color: red;
}
.Send__checkbox__input:focus + .Send__checkbox__slider {
  -webkit-box-shadow: 0 0 1px red;
          box-shadow: 0 0 1px red;
}
.Send__checkbox__input:checked + .Send__checkbox__slider:before {
  -webkit-transform: translateX(2vw);
          transform: translateX(2vw);
}
.Send__table__footer {
  text-align: center;
}
.Send__table__footer td {
  vertical-align: bottom;
}
.Send__button {
  /* margin: 1vw; */
  border: none;
  padding: 0.5vw 1vw;
  font-size: 3vw;
  line-height: 3vw;
  font-family: localImpact;
  background: white;
  color: black;
  cursor: pointer;
  -webkit-transition: 0.2s;
  transition: 0.2s;
  -webkit-box-shadow: 0 0 8px 0 white;
          box-shadow: 0 0 8px 0 white;
  width: 100%;
}
.Send__button:hover {
  background: red;

  -webkit-box-shadow: 0 0 8px 0 red;

          box-shadow: 0 0 8px 0 red;
}

.Preferences {
  position: absolute;
  margin: auto;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  width: 70vw;
  height: 35vw;
  opacity: 0.6;
  -webkit-box-shadow: 0 0 8px 0 black;
          box-shadow: 0 0 8px 0 black;
  background-color: black;
  color: white;
}
.Preferences__table {
  width: 100%;
  height: 100%;
  border-collapse: collapse;
}
.Preferences__nav {
  width: 20%;
  vertical-align: top;
  padding: 1vw 0.5vw;
}
.Preferences__tab {
  border-left: 0.5vw solid black;
  border-right: 0.5vw solid black;
  color: white;
}
.Preferences__tab > div {
  cursor: pointer;
  padding: 0 0.5vw;
  font-size: 2vw;
}
.Preferences__tab > div:hover {
  background: white;
  color: black;
}
.Preferences__tab-active {
  border-left: 0.5vw solid red;
  color: red;
}
.Preferences__tab-active > div:hover {
  color: red;
}
.Preferences__content {
  background: darkgray;
}



















